<template>
    <div>
        /board확인용 텍스트
        <b-table striped hover :items="board">
        </b-table>
    </div>
</template>

<script>
import axios from "@/axios.js"
export default {
    data() {
        return {
            board:[{
                일련번호:{content}, 곡명: "testmusic", 아티스트: "testname", 앨범명: "testalbum", 발매일자: "testdate", 재생버튼: "testplay", 다운로드: "testdownload"
            }]
        }
    },
    mounted() {
        axios.get("/api/board")
        .then(res=>{
            this.content = res.data;
        });
    }
}
</script>